import React, { useEffect, useState } from "react";
import { Typography, Container } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import { TextDecrypt } from "./TextDecrypt";
import Resume from "../../settings/resume.json";
import { FirstName, LastName } from "../../utils/getName";

function useTypewriter(words, typeSpeed = 50, pause = 1000) {
  const [index, setIndex] = useState(0); // index of word
  const [subIndex, setSubIndex] = useState(0); // index of letter
  const [forward, setForward] = useState(true);
  const [display, setDisplay] = useState("");
  useEffect(() => {
    if (forward && subIndex === words[index].length + 1) {
      setTimeout(() => setForward(false), pause);
      return;
    }
    if (!forward && subIndex === 0) {
      setForward(true);
      setIndex((prev) => (prev + 1) % words.length);
      return;
    }
    const timeout = setTimeout(() => {
      setSubIndex((prev) => prev + (forward ? 1 : -1));
      setDisplay(words[index].substring(0, subIndex));
    }, forward ? typeSpeed : 30);
    return () => clearTimeout(timeout);
  }, [subIndex, index, forward, words, typeSpeed, pause]);
  useEffect(() => {
    setDisplay(words[index].substring(0, subIndex));
  }, [subIndex, index, words]);
  return display;
}

const useStyles = makeStyles((theme) => ({
  main: {
    maxWidth: '100vw',
    marginTop: "auto",
    marginBottom: "auto",
  },
  heading: {
    marginLeft: theme.spacing(50),
    "@media (max-width: 768px)": {
      marginLeft: theme.spacing(10),
    },
  },
  jobs: {
    "@media (max-width: 768px)": {
      fontSize: '3rem',
    },
  },
}));

export const Content = () => {
  const classes = useStyles();
  const animatedText = useTypewriter(
    [Resume.basics.job1, Resume.basics.job2],
    50,
    1000
  );

  return (
    <Container component="main" className={classes.main} maxWidth="md">
      <div className={classes.heading}>
        <Typography variant="h5" component="h2">
            <TextDecrypt text={`${FirstName} ${LastName}`} />
        </Typography>
        <Typography variant="h1" component="h1" className={classes.jobs}>
          <span>{animatedText}<span style={{borderRight: '2px solid', marginLeft: 2}}>&nbsp;</span></span>
        </Typography>
      </div>
    </Container>
  );
};
